.home{
    display: flex;
    position: relative;
}

.home img{
    width: 100%;
    animation: slide 0.5s forwards;
    animation-delay: 2s;
}
.home button{
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    cursor: pointer;
    color: white;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 100px;
    font-size: large;
    margin: 0px 10px 0px 10px;
}

.home button:hover{
    background-color: white;
    color: black;
}

.next{
    right: 0;
} 

.caption{
    background-color: rgba(0, 0, 0, 0.637);
    color: white;
    text-align: left;
    position: absolute;
    margin-left: 15%;
    bottom: 30%;
    font-weight: 900;
    font-size: 26px;
    width: 50%;
}

.dot{
    position: absolute;
    bottom: 20%;
    display: flex;
    justify-content: center;
    width: 100%;
}

.dot button{
    margin-left: 5px;
    position: relative;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: rgb(88, 88, 88);
    border: none;
}

.active button{
    background-color: white;
}
