.navbar{
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 50px;
    align-items: center;
}

.title{
    display: flex;
    margin-left: 15%;
    font-weight: 900;
}

.nav{
    display: flex;
    width: 40%;
}

.nav button{
    background-color: transparent;
    border: none;
    height: 50px;
    cursor: pointer;
    margin-right: 10px;
}

.nav button:hover{
    background-color:rgb(224, 224, 224);
    height: 50px;
}

.about{
    display: inline-block;
    position: relative;
}

.dropdown{
    /* display: none; */
    position: absolute;
    background-color: white;
    min-width: 160px;
    z-index: 1;
    
}

.dropdown button{
    width: 100%;
    text-align: left;
    padding-left: 10px;
}

.dropdown button:hover{
    background-color: rgba(0, 0, 0, 0.705);
    color: white;
}